import React, { useState, useMemo } from 'react';
import { LineChart, Line, XAxis, YAxis, CartesianGrid, Tooltip, Legend, ResponsiveContainer, BarChart, Bar, ComposedChart, Scatter, ScatterChart } from 'recharts';
import { TrendingUp, TrendingDown, BarChart3, Activity, Calculator } from 'lucide-react';

const VolumeCorrelationAnalyzer = () => {
  const [selectedStock, setSelectedStock] = useState('');
  const [timeframe, setTimeframe] = useState('30');
  
  //       
  const generateSampleData = (days = 30) => {
    const data = [];
    let price = 60000;
    let volume = 15000000;
    let obv = 0;
    
    for (let i = 0; i < days; i++) {
      const date = new Date();
      date.setDate(date.getDate() - (days - i));
      
      //    (  )
      const priceChange = (Math.random() - 0.5) * 0.06; // 3% 
      price = Math.max(price * (1 + priceChange), 45000);
      
      //    (   )
      const volumeMultiplier = 1 + (Math.abs(priceChange) * 2) + (Math.random() - 0.5) * 0.4;
      volume = Math.max(volume * volumeMultiplier, 5000000);
      
      // OBV 
      if (i > 0) {
        const prevPrice = data[i-1].price;
        if (price > prevPrice) {
          obv += volume;
        } else if (price < prevPrice) {
          obv -= volume;
        }
      }
      
      data.push({
        date: date.toLocaleDateString('ko-KR', { month: 'short', day: 'numeric' }),
        price: Math.round(price),
        volume: Math.round(volume),
        obv: obv,
        priceChange: i > 0 ? ((price - data[i-1].price) / data[i-1].price * 100) : 0,
        volumeMA: i >= 5 ? data.slice(Math.max(0, i-4), i+1).reduce((sum, item) => sum + item.volume, 0) / 5 : volume
      });
    }
    
    return data;
  };

  const calculateCorrelation = (x, y) => {
    const n = x.length;
    const sumX = x.reduce((a, b) => a + b, 0);
    const sumY = y.reduce((a, b) => a + b, 0);
    const sumXY = x.reduce((total, xi, i) => total + xi * y[i], 0);
    const sumXX = x.reduce((total, xi) => total + xi * xi, 0);
    const sumYY = y.reduce((total, yi) => total + yi * yi, 0);
    
    const numerator = n * sumXY - sumX * sumY;
    const denominator = Math.sqrt((n * sumXX - sumX * sumX) * (n * sumYY - sumY * sumY));
    
    return denominator === 0 ? 0 : numerator / denominator;
  };

  // useMemo       .
  // timeframe selectedStock   .
  const { data: stockData, correlationValue } = useMemo(() => {
    // selectedStock       .
    const data = generateSampleData(parseInt(timeframe, 10));
    
    let correlation = 0;
    if (data.length > 1) {
      //  ''   .
      const priceChanges = data.map(d => d.priceChange).slice(1); //    0 
      const volumes = data.map(d => d.volume).slice(1);
      correlation = calculateCorrelation(priceChanges, volumes);
    }
    
    return { data, correlationValue: correlation };
  }, [selectedStock, timeframe]);

  const getCorrelationColor = (corr) => {
    if (corr > 0.7) return 'text-green-600';
    if (corr > 0.3) return 'text-yellow-600';
    if (corr > -0.3) return 'text-gray-600';
    if (corr > -0.7) return 'text-orange-600';
    return 'text-red-600';
  };

  const getCorrelationText = (corr) => {
    if (corr > 0.7) return '  ';
    if (corr > 0.3) return ' ';
    if (corr > -0.3) return ' ';
    if (corr > -0.7) return ' ';
    return '  ';
  };

  const stockOptions = [
    '', 'SK', 'LG', 'NAVER', '',
    'LG', '', 'POSCO', 'SDI', 'KB'
  ];

  return (
    <div className="w-full max-w-7xl mx-auto p-6 bg-white">
      <div className="mb-8">
        <h1 className="text-3xl font-bold text-gray-800 mb-2">-   </h1>
        <p className="text-gray-600">      </p>
      </div>

      {/*   */}
      <div className="grid grid-cols-1 md:grid-cols-3 gap-4 mb-6">
        <div className="bg-gray-50 p-4 rounded-lg">
          <label className="block text-sm font-medium text-gray-700 mb-2"> </label>
          <select 
            value={selectedStock} 
            onChange={(e) => setSelectedStock(e.target.value)}
            className="w-full p-2 border border-gray-300 rounded-md focus:ring-2 focus:ring-blue-500"
          >
            {stockOptions.map(stock => (
              <option key={stock} value={stock}>{stock}</option>
            ))}
          </select>
        </div>
        
        <div className="bg-gray-50 p-4 rounded-lg">
          <label className="block text-sm font-medium text-gray-700 mb-2"> </label>
          <select 
            value={timeframe} 
            onChange={(e) => setTimeframe(e.target.value)}
            className="w-full p-2 border border-gray-300 rounded-md focus:ring-2 focus:ring-blue-500"
          >
            <option value="15">15</option>
            <option value="30">30</option>
            <option value="60">60</option>
            <option value="90">90</option>
          </select>
        </div>
        
        <div className="bg-blue-50 p-4 rounded-lg">
          <div className="flex items-center mb-2">
            <Calculator className="h-5 w-5 text-blue-600 mr-2" />
            <span className="text-sm font-medium text-gray-700"></span>
          </div>
          <div className={`text-2xl font-bold ${getCorrelationColor(correlationValue)}`}>
            {correlationValue.toFixed(3)}
          </div>
          <div className={`text-sm ${getCorrelationColor(correlationValue)}`}>
            {getCorrelationText(correlationValue)}
          </div>
        </div>
      </div>

      {/*    */}
      <div className="grid grid-cols-1 md:grid-cols-4 gap-4 mb-6">
        <div className="bg-gradient-to-br from-blue-50 to-blue-100 p-4 rounded-lg">
          <div className="flex items-center justify-between">
            <div>
              <p className="text-sm text-blue-600 font-medium"> </p>
              <p className="text-xl font-bold text-blue-800">
                {stockData.length > 0 ? stockData[stockData.length - 1].price.toLocaleString() : 0}
              </p>
            </div>
            <TrendingUp className="h-8 w-8 text-blue-600" />
          </div>
        </div>
        
        <div className="bg-gradient-to-br from-green-50 to-green-100 p-4 rounded-lg">
          <div className="flex items-center justify-between">
            <div>
              <p className="text-sm text-green-600 font-medium"> </p>
              <p className="text-xl font-bold text-green-800">
                {stockData.length > 0 ? (stockData[stockData.length - 1].volume / 10000).toFixed(0) : 0}
              </p>
            </div>
            <BarChart3 className="h-8 w-8 text-green-600" />
          </div>
        </div>
        
        <div className="bg-gradient-to-br from-purple-50 to-purple-100 p-4 rounded-lg">
          <div className="flex items-center justify-between">
            <div>
              <p className="text-sm text-purple-600 font-medium">OBV</p>
              <p className="text-xl font-bold text-purple-800">
                {stockData.length > 0 ? (stockData[stockData.length - 1].obv / 100000000).toFixed(1) : 0}
              </p>
            </div>
            <Activity className="h-8 w-8 text-purple-600" />
          </div>
        </div>
        
        <div className="bg-gradient-to-br from-orange-50 to-orange-100 p-4 rounded-lg">
          <div className="flex items-center justify-between">
            <div>
              <p className="text-sm text-orange-600 font-medium"> </p>
              <p className="text-xl font-bold text-orange-800">
                {stockData.length > 0 ? 
                  (stockData[stockData.length - 1].priceChange > 0 ? '+' : '') + 
                  stockData[stockData.length - 1].priceChange.toFixed(2) : 0}%
              </p>
            </div>
            <TrendingDown className="h-8 w-8 text-orange-600" />
          </div>
        </div>
      </div>

      {/*     */}
      <div className="bg-white p-6 rounded-lg shadow-md mb-6">
        <h2 className="text-xl font-semibold mb-4">   </h2>
        <ResponsiveContainer width="100%" height={400} key={`${selectedStock}-${timeframe}-composed`}>
          <ComposedChart data={stockData}>
            <CartesianGrid strokeDasharray="3 3" />
            <XAxis dataKey="date" />
            <YAxis yAxisId="price" orientation="left" />
            <YAxis yAxisId="volume" orientation="right" />
            <Tooltip 
              formatter={(value, name) => [
                name === 'price' ? `${value.toLocaleString()}` : 
                name === 'volume' ? `${(value/10000).toFixed(0)}` : value,
                name === 'price' ? '' : 
                name === 'volume' ? '' : name
              ]}
            />
            <Legend />
            <Line yAxisId="price" type="monotone" dataKey="price" stroke="#2563eb" strokeWidth={3} name="" />
            <Bar yAxisId="volume" dataKey="volume" fill="#10b981" opacity={0.7} name="" />
          </ComposedChart>
        </ResponsiveContainer>
      </div>

      {/* OBV  */}
      <div className="bg-white p-6 rounded-lg shadow-md mb-6">
        <h2 className="text-xl font-semibold mb-4">OBV (On Balance Volume) </h2>
        <ResponsiveContainer width="100%" height={300} key={`${selectedStock}-${timeframe}-obv`}>
          <LineChart data={stockData}>
            <CartesianGrid strokeDasharray="3 3" />
            <XAxis dataKey="date" />
            <YAxis />
            <Tooltip 
              formatter={(value) => [`${(value/100000000).toFixed(1)}`, 'OBV']}
            />
            <Legend />
            <Line type="monotone" dataKey="obv" stroke="#7c3aed" strokeWidth={3} name="OBV" />
          </LineChart>
        </ResponsiveContainer>
      </div>

      {/*   */}
      <div className="bg-white p-6 rounded-lg shadow-md mb-6">
        <h2 className="text-xl font-semibold mb-4">-  </h2>
        <ResponsiveContainer width="100%" height={300} key={`${selectedStock}-${timeframe}-scatter`}>
          <ScatterChart data={stockData}>
            <CartesianGrid strokeDasharray="3 3" />
            <XAxis 
              dataKey="priceChange" 
              type="number" 
              domain={['dataMin', 'dataMax']}
              name="(%)"
            />
            <YAxis 
              dataKey="volume" 
              type="number" 
              domain={['dataMin', 'dataMax']}
              name=""
            />
            <Tooltip 
              formatter={(value, name) => [
                name === 'volume' ? `${(value/10000).toFixed(0)}` : `${value.toFixed(2)}%`,
                name === 'volume' ? '' : ''
              ]}
            />
            <Scatter name=" " dataKey="volume" fill="#f59e0b" />
          </ScatterChart>
        </ResponsiveContainer>
      </div>

      {/*    */}
      <div className="bg-gray-50 p-6 rounded-lg">
        <h2 className="text-xl font-semibold mb-4">  </h2>
        <div className="grid grid-cols-1 md:grid-cols-2 gap-6">
          <div>
            <h3 className="text-lg font-medium mb-2"> </h3>
            <p className="text-gray-700 mb-2">
               {selectedStock}     <span className={`font-bold ${getCorrelationColor(correlationValue)}`}>
                {correlationValue.toFixed(3)}
              </span>, <span className={getCorrelationColor(correlationValue)}>
                {getCorrelationText(correlationValue)}
              </span> .
            </p>
            {correlationValue > 0.5 && (
              <p className="text-green-700 text-sm">
                       <br/>
                     
              </p>
            )}
            {correlationValue < -0.5 && (
              <p className="text-red-700 text-sm">
                       <br/>
                      
              </p>
            )}
            {Math.abs(correlationValue) <= 0.5 && (
              <p className="text-gray-700 text-sm">
                      <br/>
                       
              </p>
            )}
          </div>
          
          <div>
            <h3 className="text-lg font-medium mb-2">OBV </h3>
            <p className="text-gray-700 mb-2">
              OBV         .
            </p>
            <p className="text-gray-600 text-sm">
               OBV  +  :  ,   <br/>
               OBV  +  :  ,  <br/>
               OBV  +  :  ,  <br/>
               OBV  +  :  ,   
            </p>
          </div>
        </div>
        
        <div className="mt-4 p-4 bg-yellow-50 border-l-4 border-yellow-400">
          <p className="text-yellow-800 text-sm">
            <strong> :</strong>      . 
                ,  ,        .
          </p>
        </div>
      </div>
    </div>
  );
};

export default VolumeCorrelationAnalyzer;